// eslint-disable-next-line
import { Card, CardContent } from "@material-ui/core"
import React from "react";
import Scrollbars from "react-custom-scrollbars";
import KSService from "../../../../services/KSService";
import KatalonContent from "../../../../variables/KatalonContent";
import KatalonEndpoints from "../../../../variables/KatalonEndpoints";
import NewsPlaceholder from "../NewsPlaceholder";
import { useStyles } from "../StyleClasses"
import StartPageArea from "../../../../variables/StartPageArea"
import { LearnMoreAction } from "./LearnMoreAction";
import ForumIcon from '../../../../../assets/icon/forum@2x.png';
import HotTopics from "../HotTopics";
import { CustomCardHeader } from "./CustomCardHeader";

const DEFAULT_FORUM_READMORE_LINK = 'https://forum.katalon.com/top/monthly?page=1&per_page=50';

export const Discussion = ({ elevation }) => {
    const [forumTopics, setForumTopics] = React.useState();
    const [forumTopicsReadMoreLink, setForumTopicsReadMoreLink] = React.useState(DEFAULT_FORUM_READMORE_LINK);
    const classes = useStyles();

    const resolveForumTopics = (forumTopicsInfo) => {
        return forumTopicsInfo.topic_list.topics.filter((topic) => topic.tags.includes('start-page'));
    }

    const fetchForumTopics = async () => {
        await KSService.fetchContent(KatalonContent.ForumTopics)
            .then((forumTopicsInfo) => {
                const hotTopics = resolveForumTopics(forumTopicsInfo);
                setForumTopics(hotTopics);
                const readMoreTopicsLink = `${KatalonEndpoints.ForumPage}${forumTopicsInfo.topic_list.more_topics_url}`;
                setForumTopicsReadMoreLink(readMoreTopicsLink);
            });
    }
    React.useEffect(() => {
        setTimeout(() => {
            fetchForumTopics();
        }, 1000);
    }, []);// eslint-disable-line react-hooks/exhaustive-deps


    return <Card elevation={elevation} className={classes.card}>
        <CustomCardHeader icon={ForumIcon} title={'HOT DISCUSSIONS'} />
        <CardContent className={classes.cardContent}>
            <Scrollbars autoHide hideTracksWhenNotNeeded height="100%" className={classes.contentWrapper}>
                {forumTopics ? (
                    <HotTopics topics={forumTopics} />
                ) : (
                    <NewsPlaceholder />
                )}
            </Scrollbars>
            <div className={classes.contentWrapperNoScroll}>
                {forumTopics ? (
                    <HotTopics topics={forumTopics} />
                ) : (
                    <NewsPlaceholder />
                )}
            </div>
        </CardContent>
        <LearnMoreAction area={StartPageArea.ForumTopics} href={forumTopicsReadMoreLink} />
    </Card>
}