import { Card, CardActions, CardContent, Grid, IconButton } from "@material-ui/core"
import { ThumbDownAlt, ThumbUpAlt } from "@material-ui/icons"
import React, { useState } from "react"
import Scrollbars from "react-custom-scrollbars"
import KSActionService from "../../../../services/KSActionService"
import KSService from "../../../../services/KSService"
import KatalonContent from "../../../../variables/KatalonContent"
import { RenderMarkdown } from "../RenderMarkdow"
import { useStyles } from "../StyleClasses"
import StartPageArea from "../../../../variables/StartPageArea"
import StarIcon from '../../../../../assets/icon/star@2x.png';
import StartPageLink from "../StartPageLink"
import { CustomCardHeader } from "./CustomCardHeader"

export const BestPractices = ({ elevation }) => {
    const classes = useStyles();
    const [bestPractices, setBestPractices] = useState();
    const [bestPracticesReadMoreLink, setBestPracticesReadMoreLink] = useState();
    const [userProfile, setUserProfile] = React.useState({});

    const hasReviewBestPractice = userProfile && userProfile.bestPracticeReviews
        && userProfile.bestPracticeReviews[bestPracticesReadMoreLink] != null;
    const isLikeBestPractice = hasReviewBestPractice
        && userProfile.bestPracticeReviews[bestPracticesReadMoreLink] === true;
    const likeInactiveColor = 'lightgray';
    const likeButtonColor = hasReviewBestPractice ? (isLikeBestPractice ? '#1646c7' : likeInactiveColor) : 'secondary';
    const unlikeButtonColor = hasReviewBestPractice ? (!isLikeBestPractice ? '#cd4a4a' : likeInactiveColor) : 'primary';

    const fetchBestPractices = async () => {
        await KSService.fetchContent(KatalonContent.BestPractices)
            .then((data) => {
                setBestPractices(data);
                setBestPracticesReadMoreLink();
            });
    }
    const fetchUserProfile = async () => {
        await KSService.fetchUserProfile()
            .then((profile) => {
                setUserProfile(profile || {});
            });
    }

    const handleLikeBestPractices = async () => {
        await KSActionService.reviewBestPractice(bestPracticesReadMoreLink, true)
            .then(() => {
                fetchUserProfile();
            });
    }

    const handleUnlikeBestPractices = async () => {
        await KSActionService.reviewBestPractice(bestPracticesReadMoreLink, false)
            .then(() => {
                fetchUserProfile();
            });
    }
    React.useEffect(() => {
        setTimeout(() => {
            fetchBestPractices();
            fetchUserProfile();
        }, 1000);// eslint-disable-line react-hooks/exhaustive-deps
    }, []);

    return <>
        <Card elevation={elevation} className={classes.card}>
            <CustomCardHeader icon={StarIcon} title={'BEST PRACTICES'} />
            <CardContent className={classes.cardContent}>
                <Scrollbars autoHide hideTracksWhenNotNeeded className={classes.contentWrapper}>
                    <RenderMarkdown content={bestPractices} area={StartPageArea.BestPractices} components={{
                        img: ({ node, ...props }) => <img {...props} style={{ width: 'calc(100%)' }} alt="markdown-img" />
                    }} />
                </Scrollbars>
                <div className={classes.contentWrapperNoScroll}>
                    <RenderMarkdown content={bestPractices} area={StartPageArea.BestPractices} components={{
                        img: ({ node, ...props }) => <img {...props} style={{ width: 'calc(100%)' }} alt="markdown-img" />
                    }} />
                </div>
            </CardContent>
            <CardActions>
                <Grid container justifyContent="space-between" alignItems="center" spacing={2}>
                    <Grid item>
                        <StartPageLink
                            area={StartPageArea.LikeBestPractices}
                            asLink={false}
                            onClick={handleLikeBestPractices}
                        >
                            <IconButton aria-label="Like" size="small">
                                <ThumbUpAlt color="primary" style={{ color: likeButtonColor }} />
                            </IconButton>
                        </StartPageLink>
                        <StartPageLink
                            area={StartPageArea.UnlikeBestPractices}
                            asLink={false}
                            onClick={handleUnlikeBestPractices}
                        >
                            <IconButton aria-label="Dislike" size="small">
                                <ThumbDownAlt color="secondary" style={{ color: unlikeButtonColor }} />
                            </IconButton>
                        </StartPageLink>
                    </Grid>
                    <Grid item>
                        <StartPageLink
                            area={StartPageArea.BestPractices}
                            href={bestPracticesReadMoreLink}
                        >
                            Learn more
                        </StartPageLink>
                    </Grid>
                </Grid>
            </CardActions>
        </Card>
    </>
}
