import React from 'react';
import rehypeRaw from "rehype-raw";
import KSTrackingService from "../../../services/KSTrackingService";
import { useStyles } from "./StyleClasses";
import { ReactMarkdown } from 'react-markdown/lib/react-markdown';
import NewsPlaceholder from './NewsPlaceholder';


export const RenderMarkdown = ({ content, area, components }) => {
    const classes = useStyles();
    const handleClickCapture = async (event, area) => {
        const url = event.target.href;
        await KSTrackingService.trackStartPage(area, url);
    }

    return content ? (
        <div onClickCapture={(event) => handleClickCapture(event, area)}>
            <ReactMarkdown
                children={content}
                rehypePlugins={[rehypeRaw]}
                className={classes.skeleton}
                components={components}
            />
        </div>
    ) : (
        <NewsPlaceholder />
    );
}