export const getTitleIcon = (icon) => {
    return <img src={icon} alt="icon" style={{ height: '1rem', verticalAlign: 'text-top' }} />;
}


export function findFirstLink(markdown = '') {
    const matches = markdown.match(
        /\[.*?\]\((https?:\/\/(www\.)?[-a-zA-Z0-9@:%._+~#=]{1,256}\.[a-zA-Z0-9()]{1,6}\b([-a-zA-Z0-9()@:%_+.~#?&//=]*))\)/i
    );
    return matches && matches[1];
}