import { makeStyles } from '@material-ui/core';
import React from 'react';
import StartPageArea from '../../../variables/StartPageArea';
import StartPageLink from './StartPageLink';


const useStyles = makeStyles({
  root: {
  },
  listItem: {
  }
});

export default function HotTopics({ topics }) {
  const classes = useStyles();

  return (
    topics ? (
      <ul className={classes.root}>
        {topics.map((topic) => (
          <li key={topic.id} className={classes.listItem}>
            <StartPageLink area={StartPageArea.ForumTopics} href={`https://forum.katalon.com/t/${topic.slug}`} asLink={false}>
              {topic.fancy_title || topic.title}
            </StartPageLink>
          </li>
        ))}
      </ul>
    ) : (
      null
    )
  );
}
