
import { Card, CardActions, CardContent, Grid } from "@material-ui/core"
import React from "react"
import Scrollbars from "react-custom-scrollbars"
import KSActionService from "../../../../services/KSActionService"
import KSService from "../../../../services/KSService"
import KatalonContent from "../../../../variables/KatalonContent"
import StartPageArea from "../../../../variables/StartPageArea"
import { RenderMarkdown } from "../RenderMarkdow"
import StartPageLink from "../StartPageLink"
import { useStyles } from "../StyleClasses"
import { findFirstLink } from "./Utils"
import { faAngleDoubleUp } from '@fortawesome/free-solid-svg-icons';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import CheckIcon from '../../../../../assets/icon/check@2x.png';
import { CustomCardHeader } from "./CustomCardHeader"

export const Hightlights = ({ elevation }) => {
    const classes = useStyles();
    const [releaseHighlights, setReleaseHighlights] = React.useState();
    const [releaseHighlightsReadMoreLink, setReleaseHighlightsReadMoreLink] = React.useState();

    const fetchReleaseHighlights = async () => {
        await KSService.fetchContent(KatalonContent.ReleaseHighlights)
            .then((data) => {
                setReleaseHighlights(data);
                setReleaseHighlightsReadMoreLink(findFirstLink(data));
            });
    }
    const handleUpgradeClick = async () => {
        await KSActionService.checkForUpdate();
    }

    React.useEffect(() => {
        setTimeout(() => {
            fetchReleaseHighlights();
        }, 1000);
    }, []);
    return <Card elevation={elevation} className={classes.card}>
        <CustomCardHeader icon={CheckIcon} title={'RELEASE HIGHLIGHTS'} />
        <CardContent className={classes.cardContent}>
            <Scrollbars autoHide hideTracksWhenNotNeeded height="100%" className={classes.contentWrapper}>
                <RenderMarkdown content={releaseHighlights} area={StartPageArea.ReleaseHighlights} />
            </Scrollbars>
            <div className={classes.contentWrapperNoScroll}>
                <RenderMarkdown content={releaseHighlights} area={StartPageArea.ReleaseHighlights} />
            </div>
        </CardContent>
        <CardActions>
            <Grid container justifyContent="space-between" alignItems="center" spacing={2}>
                <Grid item>
                    <StartPageLink
                        onClick={handleUpgradeClick}
                        area={StartPageArea.Upgrade}
                        asButton
                    >
                        Update now&nbsp;<FontAwesomeIcon icon={faAngleDoubleUp} style={{ marginBottom: 3 }} />

                    </StartPageLink>
                </Grid>
                <Grid item>
                    <StartPageLink
                        area={StartPageArea.ReleaseHighlights}
                        href={releaseHighlightsReadMoreLink}
                    >
                        Learn more
                    </StartPageLink>
                </Grid>
            </Grid>
        </CardActions>
    </Card>
}