// eslint-disable-next-line
import { Card, CardContent } from "@material-ui/core"
import React from "react"
import Scrollbars from "react-custom-scrollbars"
import KSService from "../../../../services/KSService"
import { RenderMarkdown } from "../RenderMarkdow"
import { useStyles } from "../StyleClasses"
import { findFirstLink } from "./Utils"
import StartPageArea from "../../../../variables/StartPageArea"
import KatalonContent from "../../../../variables/KatalonContent"
import { LearnMoreAction } from "./LearnMoreAction"
import OvalIcon from '../../../../../assets/icon/oval@2x.png';
import { CustomCardHeader } from "./CustomCardHeader"

export const News = ({ elevation }) => {
    const classes = useStyles();
    const [news, setNews] = React.useState();
    const [newsReadMoreLink, setNewsReadMoreLink] = React.useState();

    const fetchNews = async () => {
        await KSService.fetchContent(KatalonContent.News)
            .then((data) => {
                setNews(data);
                setNewsReadMoreLink(findFirstLink(data));
            });
    }

    React.useEffect(() => {
        setTimeout(() => {
            fetchNews();
        }, 1000);
    }, []);

    return <Card elevation={elevation} className={classes.card}>
        <CustomCardHeader icon={OvalIcon} title={'NEWS'} />
        <CardContent className={classes.cardContent}>
            <Scrollbars autoHide hideTracksWhenNotNeeded height="100%" className={classes.contentWrapper}>
                <RenderMarkdown area={StartPageArea.News} content={news} />
            </Scrollbars>
            <div className={classes.contentWrapperNoScroll}>
                <RenderMarkdown area={StartPageArea.News} content={news} />
            </div>
        </CardContent>
        <LearnMoreAction area={StartPageArea.News} href={newsReadMoreLink} />

    </Card>
}