import { CardHeader } from "@material-ui/core";
import { useStyles } from "../StyleClasses"
import { getTitleIcon } from "./Utils"

export const CustomCardHeader = ({ icon, title }) => {
    const headerClass = useStyles().cardHeader;
    return <CardHeader
        subheader={(
            <div>{getTitleIcon(icon)} {title}</div>
        )}
        className={headerClass}
    />
}